import React from 'react'
import PropTypes from 'prop-types'
import { graphql } from 'gatsby'

import "@suziwen/gitalk/dist/gitalk.css";

import SEO from '../components/seo'
import Layout from '../components/layout'
import Post from '../components/post'

import Loadable from "@loadable/component"

const GitalkComponent = Loadable(() => import('@suziwen/gitalk/dist/gitalk-component'))

const BlogPostTemplate = ({ data, pageContext }) => {
  const {
    frontmatter: { title, date, author, coverImage, summary, tags },
    excerpt: autoExcerpt,
    fields,
    id,
    html,
    tableOfContents,
  } = data.markdownRemark
  const { next, previous } = pageContext
  const { frontmatter } = data.markdownRemark
  // console.log('front matter', frontmatter)
  const gitalkConfig = typeof GATSBY_GITALK_CONFIG !== 'undefined'? GATSBY_GITALK_CONFIG: null

  return (
    <Layout>
      <SEO title={title} description={summary || autoExcerpt} />
      {/* <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/prismjs/themes/prism-tomorrow.css"></link> */}
      <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/prism-themes/themes/prism-vsc-dark-plus.css"></link>
      <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/katex@0.12.0/dist/katex.min.css" integrity="sha384-AfEj0r4/OFrOo5t7NnNe46zW/tFgW6x/bCJG8FqQCEo3+Aro6EYUG4+cU+KJWu/X" crossorigin="anonymous"></link>

      {frontmatter.use_toc ? (
        <div id="toc">
          <div dangerouslySetInnerHTML={{ __html: tableOfContents }}></div>
        </div>
      ) : null}
      <Post
        key={id}
        title={title}
        date={date}
        path={fields.slug}
        author={author}
        coverImage={coverImage}
        html={html}
        tags={tags}
        previousPost={previous}
        nextPost={next}
      />
      {gitalkConfig? <GitalkComponent options={{
        ...gitalkConfig,
        id: fields.slug,
      }}></GitalkComponent>: null}
    </Layout>
  )
}

export default BlogPostTemplate

BlogPostTemplate.propTypes = {
  data: PropTypes.object.isRequired,
  pageContext: PropTypes.shape({
    next: PropTypes.object,
    previous: PropTypes.object,
  }),
}

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug:  { eq: $slug } }) {
      id
      html
      excerpt
      tableOfContents
      fields {
        slug
        featureImageSrc
      }
      frontmatter {
        title
        date(formatString: "YYYY-MM-DD")
        author
        tags
        use_toc
        coverImage {
          childImageSharp {
            fluid(maxWidth: 800) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`
